.rmdb-moviethumb img {
  width: auto;
  height: auto;
  max-width: 100%;
  max-height: 100%;
  transition: all 0.3s;
  box-sizing: border-box;
}

.clickable {
  cursor: pointer;
}

.clickable:hover {
  opacity: 0.8;
}

@media screen and (max-width: 640px) {
  .rmdb-moviethumb img {
     width: 0;
     height: 0;
  }
}