.rmdb-movieinfo {
  background-size: cover !important;
  background-position: center !important;
  width: 100%;
  height: 600px;
  padding: 40px 20px;
  box-sizing: border-box;
  animation: animateMovieinfo 1s;
}

@keyframes animateMovieinfo {
  from {
      opacity:0;
  }
  to {
      opacity:1;
  }
}

.rmdb-movieinfo-content {
  max-width: 1280px;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  background: rgb(0, 0, 0, 0.7);
  position: relative;
}

.rmdb-movieinfo-thumb {
  width: 350px;
  height: 100%;
  overflow: hidden;
  position: absolute;
  left: 0px;
}

.rmdb-movieinfo-text {
  font-family: Arial, Helvetica, sans-serif;
  height: 100%;
  width: auto;
  padding: 40px;
  color: #fff;
  overflow: hidden;
  box-sizing: border-box;
  position: absolute;
  left: 360px;
}

.rmdb-movieinfo-text h1 {
  font-family: 'Abel', sans-serif;
  font-size:48px;
  margin: 0;
}

.rmdb-movieinfo-text h3 {
  font-size: 16px;
  line-height: 0;
  margin-top: 30px;
}

.rmdb-movieinfo-text p {
  font-family: 'Abel', sans-serif;
  font-size: 18px;
  line-height: 26px;
}

.rmdb-rating {
  width: 250px;
  height: 20px;
  margin-top: 20px;
  position: relative;
}

.rmdb-score {
  position: absolute;
  margin: 0;
  right: 0px;
  top: -3px;
}

.rmdb-director {
  margin: 0;
}

.fa-film {
  position: absolute;
  bottom: 40px;
  right: 40px;
  color: #fff;
}

meter::-webkit-meter-bar {
  background: #FFF;
  width:200px;
}
meter::-webkit-meter-optimum-value {
  background: linear-gradient(to bottom, #16d47b);
}
meter::-webkit-meter-suboptimum-value {
  background: linear-gradient(to bottom, #fbb450);
}
meter::-webkit-meter-even-less-good-value {
  background: linear-gradient(to bottom, #ee5f5b);
}
@media screen and (max-width: 640px) {
  .rmdb-movieinfo{
    width: 100%;
    height: 50vh;
  }
  .rmdb-movieinfo-text {
    position: relative;
    left: 0;
    z-index: 1; 
    padding: 0 1rem;
    
  }
  .rmdb-movieinfo-thumb {
    width: 15rem;
    height: auto;
  }
  .rmdb-movieinfo-text h1 {
    font-size: 1.5rem;
  }
  .rmdb-movieinfo-text h3 {
    font-size: 0.5rem;
    line-height: 0;
    margin-top: 1rem;
  }
  .rmdb-movieinfo-content {
    width: 100%;
    height: auto;
    /* top: -1rem; */
  }
  .rmdb-movieinfo-text p {
    font-size: 0.5rem;
    line-height: 1rem;
  }
  .rmdb-rating {
  width: 8rem;
  height: 1rem;
  margin-top: 0.5rem;
  position: relative;
  }
  meter::-webkit-meter-bar {
    background: #FFF;
    width: 8rem;
  }
  .rmdb-score {
    position: absolute;
    margin: 0;
    right: -2rem;
    top: 2px;
  }
  .fa-film {
  bottom: 0;
}
}
